@import "../../../css/module/fn";
@import "../../../css/module/color";

.main{
	@include shadow();
	
	margin: 20px;
	background-color: #fff;
	
	.S3{
		margin: 0 auto 20px ;
		display: block;
		width: p2m(600);
		//height: p2m(430);
	}
	h1{
		padding: 20px;
		font-size: 20px;
	}
	h3{
		height: p2m(70);
		line-height: p2m(70);
		margin-bottom: 0;
		color: $red;
		text: {
			indent: p2m(80);

		}
		font: {
			size: p2m(28);
			weight: 600;
		}
		background:{
			repeat: no-repeat;
			position: 12px center;
			size: p2m(32) p2m(32);
		}
		
		&.h3type {
			background-image: url("./ico32-bloom.png");
		}
		&.h3time {
			background-image: url("./ico32-clock.png");
		}
		&.h3note {
			background-image: url("./ico32-book.png");
		}
		&.h3location {
			background-image: url("./ico32-location.png");
		}
		&.h3meeting {
			background-image: url("./ico32-meeting.png");
		}
	}
	.txt{
		//text-indent: p2m(80);
		//margin-top: p2m(-20);
		//margin-left: p2m(16);
		margin: p2m(20);
	}
	.pMsg{
		margin: p2m(16) 1em 2em ;
		text: {
			//indent: 2em;
		}
		background:{
			//image: url("./ico32-book.png");
			repeat: no-repeat;
			position: left 3px ;
			size: p2m(32) p2m(32);
		}
	}
	
	
	.mui-popup-button {
		color: #fff;
		background-color: $red;
		font-size: p2m(26);
		box-shadow: inset 0 3px 3px 0 rgba(0, 0, 0, 0.2);

		
		&:first-child {
			border-bottom-left-radius:   4px;
		}
		&:last-child {
			border-bottom-right-radius: 4px ;
		}
		&:after {
			background-color: white;
			width: 2px;
		}
		
		&.green{
			background-color: #65b658;
		}
		&.blue{
			background-color: #4069d0;
		}
		&.gray{
			color: #999;
			background-color: #ddd;
		}
	}
}

